

/***************************************************

     ## [ Index ]
    
    I - Background Manipulation
   II - Text Manipulation
  III - Width and Height Manipulation
   IV - Image Manipulation

****************************************************/

    .body{
        font-family: 'Inter', sans-serif !important;
        margin:0;
    }

/***************************************************

    I - Background Manipulation

****************************************************/


    .bg-red-orange{
        background-color:#F55A5A;
    }

    .bg-darker{
        background-color:#2B283A;
    }

/***************************************************

   II - Text Manipulation

****************************************************/
    .text-darker{
        color:#2B283A;
    }

    .letter-space{
        letter-spacing: 0.17em;
    }

    .country-text {
        font-style: normal;
        font-weight: 400;
        font-size: 13px;
        line-height: 12px;
        letter-spacing: 0.17em;
    }

/***************************************************

 III - Width and Height Manipulation

****************************************************/

    .w-max-540{
        max-width: 540px;
    }

    .h-280{
        height:280px;
    }

/***************************************************

 IV - Image Manipulation

****************************************************/
    .image{
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }

/***************************************************/
